import { Component, Prop, Event, EventEmitter, h } from "@stencil/core";

type InputState = "default" | "error" | "success";

@Component({
  tag: "stc-input",
  shadow: false,
  scoped: true,
  styleUrl: "stc-input.css",
})
export class StcInput {
  @Prop() label = "";
  @Prop() placeholder = "";
  @Prop({ mutable: true }) value = "";
  @Prop() disabled = false;
  @Prop() state: InputState = "default";
  @Prop() helperText = "";

  /** Emits on each input */
  @Event({ eventName: "stc-input" })
  stcInput!: EventEmitter<{ value: string }>;

  private onInput = (e: Event) => {
    const v = (e.target as HTMLInputElement).value;
    this.value = v;
    this.stcInput.emit({ value: v });
  };

  render() {
    return (
      <label>
        {this.label ? (
          <span class="stc-input__label">{this.label}</span>
        ) : null}

        <input
          class={[
            "stc-input",
            `stc-input--${this.state}`,
          ].join(" ")}
          value={this.value}
          disabled={this.disabled}
          placeholder={this.placeholder}
          onInput={this.onInput}
        />

        {this.helperText ? (
          <span
            class={[
              "stc-input__helper",
              this.state !== "default"
                ? `stc-input__helper--${this.state}`
                : "",
            ].join(" ")}
          >
            {this.helperText}
          </span>
        ) : null}
      </label>
    );
  }
}
